import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink,
} from 'react-router-dom';
import { Home, Grid, Plus, SignOut } from 'akar-icons';

import Dashboard from '../../pages/Dashboard/Dashboard';
import TableOverview from '../../pages/TableOverview/TableOverview';
import AddTable from '../../pages/addTable/AddTable';

import './appBody.css';

const AppBody = (props) => {
  const logout = async (event) => {
    event.preventDefault();
    await fetch('/auth/logout');
    window.location.reload();
  };

  return (
    <div className="app__body">
      <Router>
        <div className="body__sidebar">
          <NavLink to="/" activeClassName="sidebar__item__active" exact>
            <div className="sidebar__item">
              <Home />
            </div>
          </NavLink>
          <NavLink to="/tableOverview" activeClassName="sidebar__item__active">
            <div className="sidebar__item">
              <Grid />
            </div>
          </NavLink>
          <NavLink to="/addTable" activeClassName="sidebar__item__active">
            <div className="sidebar__item">
              <Plus />
            </div>
          </NavLink>
          <a href="/auth/logout" onClick={logout}>
            <div className="sidebar__item">
              <SignOut />
            </div>
          </a>
        </div>
        <div className="body__pages">
          <Switch>
            <Route exact path="/">
              <Dashboard />
            </Route>
            <Route path="/tableOverview">
              <TableOverview />
            </Route>
            <Route path="/addTable">
              <AddTable />
            </Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
};

export default AppBody;
