import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Plus } from 'akar-icons';
import Chart from 'chart.js/auto';
import prettyBytes from 'pretty-bytes';
import './Dashboard.css';

const Dashboard = () => {
  let [storageUsage, setStorageUsage] = useState({
    bytes: 1,
    tables: 0,
  });

  let [data, setData] = useState({
    labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Son'],
    datasets: [
      {
        label: 'Requests on that day',
        data: [0, 0, 0, 0, 0, 0, 0],
        fill: false,
        borderColor: 'rgb(86, 88, 214)',
        tension: 0.3,
      },
    ],
  });

  let [requestChart, setRequestChart] = useState(null);

  async function getStorageUsage() {
    let response = await (await fetch('/api/getTotalStorageUsage')).json();

    setStorageUsage({
      bytes: response.bytes,
      tables: response.tables,
    });

    return response;
  }

  async function getRequestData() {
    let response = await (await fetch('/api/getTotalAmountOfRequests')).json();

    setData({
      labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Son'],
      datasets: [
        {
          label: 'Requests on that day',
          data: response.requests,
          fill: false,
          borderColor: 'rgb(86, 88, 214)',
          tension: 0.3,
        },
      ],
    });

    if (requestChart) requestChart.destroy();

    // @ts-ignore
    let chart = new Chart(document.querySelector('canvas#request__graph'), {
      type: 'line',
      data: {
        labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Son'],
        datasets: [
          {
            label: 'Requests on that day',
            data: response.requests,
            fill: false,
            borderColor: 'rgb(86, 88, 214)',
            tension: 0.3,
          },
        ],
      },
    });

    setRequestChart(chart);

    return response;
  }

  useEffect(() => {
    getStorageUsage();
    getRequestData();
  }, []);

  return (
    <div className="dashboard">
      <div className="dashboard__title">
        <h1>Dashboard</h1>
      </div>
      <div className="dashboard__body">
        <div className="metrics__row">
          <div className="storage__metrics">
            <div className="storage__metrics__tableCount storage__metric">
              <div className="metrics__tableCount__number">
                {storageUsage.tables}
              </div>
              <div className="metrics__tableCount__title">no. of tables</div>
            </div>
            <div className="storage__metrics__divider">
              <div className="divider__bar"></div>
            </div>
            <div className="storage__metrics__storageUsed storage__metric">
              <div className="metrics__storageUsed__number">
                {prettyBytes(storageUsage?.bytes || 0)}
              </div>
              <div className="metrics__storageUsed__title">storage used</div>
            </div>
          </div>
          <div className="add__table" data-title="Add table">
            <Link to="/addTable">
              <Plus color="rgb(209, 209, 209)" size={32} />
            </Link>
          </div>
        </div>
        <div className="request__metrics">
          <div className="request__amount">
            <canvas
              className=""
              id="request__graph"
              height="600"
              width="1000"
            ></canvas>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
