/**
 * @function
 * @param {string} text - holds the text that should be truncated
 * @param {number} maxLength - describes at with length the text will be truncated
 * @returns {string}
 */
const truncate = (text, maxLength) => {
  if (text.length <= maxLength) return text;
  let characters = text.split('');
  characters.splice(maxLength, characters.length - maxLength);

  return `${characters.join('')}...`;
};

export default truncate;
