import React, { useState, useEffect } from 'react';
import './App.css';
import AppHeader from './components/appHeader/appHeader';
import AppBody from './components/appBody/appBody';
import UserContext from './contexts/userContext';

const AppContainer = () => {
  let [user, setUser] = useState({
    email: '',
    username: '',
    id: '',
  });

  async function getUserInfo() {
    let response = await fetch('/api/getUserInfo');
    let parsedResponse = await response.json();

    setUser(parsedResponse);

    return parsedResponse;
  }

  useEffect(() => {
    getUserInfo();
  }, []);

  return (
    <div className="app">
      <UserContext.Provider value={{ user, setUser }}>
        <AppHeader username={user['username']} />
        <AppBody />
      </UserContext.Provider>
    </div>
  );
};

const Login = () => {
  let [email, setEmail] = useState('');
  let [password, setPassword] = useState('');
  let [errorMessage, setErrorMessage] = useState('');

  async function login() {
    let response = await fetch('/auth/weblogin', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        email: email,
        password: password,
      }),
    });

    let parsedResponse = await response.json();

    if (parsedResponse.isAuth) {
      window.location.reload();
    } else {
      setErrorMessage(parsedResponse.error);
    }
  }

  return (
    <div className="login">
      <form
        method="post"
        onSubmit={(event) => {
          event.preventDefault();
          login();
        }}
      >
        <input
          type="email"
          name="email"
          onChange={(event) => {
            setEmail(event.target.value);
          }}
          placeholder="Email"
        />
        <input
          type="password"
          name="password"
          onChange={(event) => {
            setPassword(event.target.value);
          }}
          placeholder="Password"
        />
        <input type="submit" value="Login" />
        <span>{errorMessage}</span>
      </form>
    </div>
  );
};

function App() {
  let [isAuth, setIsAuth] = useState(false);

  async function isLoggedIn() {
    let response = await fetch('/auth/isAuth');
    let parsedResponse = await response.json();
    setIsAuth(parsedResponse.isAuth);
  }

  useEffect(() => {
    isLoggedIn();
  }, []);

  return <>{isAuth ? <AppContainer /> : <Login />}</>;
}

export default App;
